



















export default {
	data(){
		return {
			longitude:"",
			latitude:""
		}
		
	},
	mounted() {
		const _this = this;
		uni.getLocation({
			type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			success: function (res) {
				_this.longitude = res.longitude;
				_this.latitude = res.latitude;
				const latitude = res.latitude;
				const longitude = res.longitude;
				uni.openLocation({
					latitude: latitude,
					longitude: longitude,
					success: function () {
						console.log('success');
					}
				});
			}
		});
	}
}
